.picture-div{
    width: 288px;
    margin-top: 20px;
    &__icon{
    width: 288px;
    height: 329px;
    color: #FFFFFF;
}
    &__text{
    width: 288px;
    height: 248px;
    border: 1px solid #FCD54C;
    padding: 20px 0 20px 20px;
}
    &__title{
    margin: 0;
    display: inline-block;
    color: #FFFFFF;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    width: 244px;
    height: 70px;
}
    &__p{
    margin: 23px 0 28px 0;
    display: inline-block;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #929292;
    width: 247px;
    height: 63px;
}
    &__button{
    background-color: #272727;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    width: 91px;
    height: 24px;
    padding: 0;
    border: 0;
}
}